import { Block } from '@ali/4ever-cangjie';
import { TOGGLE_FOLD, toggleFold } from '@ali/4ever-plugin-heading-group'
import type { ToggleFoldPayload } from '@ali/4ever-plugin-heading-group'

export {
  TOGGLE_FOLD,
  toggleFold,
};

export type {
  ToggleFoldPayload,
};

export interface ClearImmediatelyPayload {
  node: Block;
}

export const CLEAR_IMMEDIATELY = Symbol('CLEAR_IMMEDIATELY');


export function clearImmediately(payload: ClearImmediatelyPayload) {
  return {
    type: CLEAR_IMMEDIATELY,
    payload,
  };
}
